




















































































import Vue from 'vue';
import { Ripple } from 'vuetify/lib/directives/';

function requestDefaultValues () {
    return {
        oas: null,
        preLearnings: null,
        skills: null,
        attitudes: null,
    }
}

export default Vue.extend({
    name: 'RomaExcelModalForm',
    props: {
        value: Boolean,
        activity: Object,
        maxWidth: {
            type: String,
            default: '90%',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
    },
    data() {
        return {
            valid: false,
            loading: false,
            showing: this.value,
            request: requestDefaultValues(),
        };
    },
    methods: {
        show(value: Boolean) {
            this.$emit('input', value);
            this.request = requestDefaultValues();
        },
        save () {
            console.log(this.request);
            this.show(false);
        }
    },
    directives: {
        Ripple
    }
});
